@import url("https://fonts.googleapis.com/css2?family=Economica:wght@400;700&family=Quicksand:wght@300;400;500&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Quicksand", "Economica", sans-serif;
}

button {
  cursor: pointer;
}

body::-webkit-scrollbar {
  display: none;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.navbar {
  display: none;
}

.active {
  background-color: #faca36;
}

header {
  padding-left: 75px;
  padding-right: 90px;
  height: 120px;
  background-color: #101311;
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 0 calc((100% - 1080px) / 2);
  padding-left: calc((100% - 1080px) / 2 - 16.7px);
}
header .logo-head {
  position: absolute;
  padding: 10px;
  background-color: #101311;
  border-radius: 50%;
  margin-top: 8px;
}
header .nav-head {
  display: flex;
  gap: 20px;
  line-height: 115px;
  margin-bottom: 2px;
  letter-spacing: 1.5px;
}
header .nav-head a {
  color: #ffffff;
  font-weight: 300;
  font-family: "Quicksand";
  font-size: 20px;
}
header .nav-head .nav-item:first-child a {
  color: #faca36;
}
header .nav-head a:hover {
  color: #faca36;
}
header .nav-head .nav-item:last-child {
  display: flex;
  gap: 14px;
  margin-left: 10px;
}
header .nav-head .nav-item:last-child a:first-child {
  color: #faca36;
}

.slider {
  height: 622px;
  background-image: linear-gradient(to left, #45413f, #e6dad4);
  background-repeat: no-repeat !important;
  background-size: cover !important;
  padding: 90px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 50px;
  gap: 129px;
  padding: 50px calc((100% - 1080px) / 2);
  padding-right: calc((100% - 1080px) / 2 - 30px);
}
.slider .slider-content {
  width: 47%;
  height: 240px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  letter-spacing: 0.89px;
  margin: 141px 0 auto auto;
  word-spacing: -0.77px;
}
.slider .slider-content__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  font-size: 70px;
  line-height: 50px;
}
.slider .slider-content__title span {
  color: #ffffff;
  font-family: "Economica";
}
.slider .slider-content__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  line-height: 1.8;
  margin-top: 10px;
  color: #ffffff;
}
.slider .slider-content .btn-order {
  width: 38%;
  height: 45px;
  border-radius: 4px;
  background-color: transparent;
  border: 2px solid #faca36;
}
.slider .slider-content .btn-order a {
  color: #faca36;
  font-weight: 300;
  font-family: "Quicksand";
  font-size: 20px;
  line-height: 40px;
  word-spacing: 1px;
  letter-spacing: 1.5px;
}
.slider .slider-content .btn-order:hover {
  background-color: #faca36;
}
.slider .slider-content .btn-order:hover a {
  color: #101311;
}
.slider .dot {
  display: flex;
  gap: 15px;
  padding-right: 24px;
}
.slider .dot button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
}

.service {
  text-align: center;
  height: 762px;
  padding: 90px calc((100% - 1080px) / 2);
}
.service__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
}
.service__title span {
  color: #000000;
  font-family: "Economica";
}
.service__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  padding: 12px 12.5%;
  line-height: 1.5;
  word-spacing: 0.7px;
  letter-spacing: 0.7px;
}
.service-list {
  display: flex;
  margin-top: 33px;
  gap: 26px;
  justify-content: center;
}
.service-list .service-item {
  width: 35.2%;
}
.service-list .service-item img {
  width: 275px;
  height: 275px;
  border-radius: 50%;
  padding: 8px;
  background-color: #ffead0;
  border: 8px solid #fff4e7;
}
.service-list .service-item__title {
  margin: 4px 0;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  font-size: 34px;
  line-height: 60px;
  color: #0a0a08;
}
.service-list .service-item__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  line-height: 30px;
  color: #444645;
  letter-spacing: 0.8px;
}

.menu {
  height: 783px;
  display: flex;
}
.menu-left {
  background-color: #101311;
  color: #e6dad4;
  padding: 88px 73px 96px calc(50% - 533px);
  text-align: center;
  width: 65%;
}
.menu-left__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  letter-spacing: 1.5px;
  padding-right: 12px;
  width: 649px;
}
.menu-left__service {
  display: flex;
  gap: 10px;
  justify-content: center;
  padding: 0 9px;
  padding-right: 29px;
  width: 649px;
}
.menu-left__service-btn {
  background-color: transparent;
  border: 2px solid #faca36;
  border-radius: 4px;
  height: 45px;
  margin-top: 21px;
  width: 33%;
}
.menu-left__service-btn a {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  color: #faca36;
  letter-spacing: 0.8px;
}
.menu-left__service-btn:hover {
  background-color: #faca36;
}
.menu-left__service-btn:hover a {
  color: #101311;
}
.menu-left-list {
  width: 649px;
  display: grid;
  gap: 55px;
  grid-template-columns: 1fr 1fr;
  margin-top: 30px;
}
.menu-left-list .list-item .order-item {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  margin-right: 10px;
  border-bottom: 1px solid #45413f;
}
.menu-left-list .list-item .order-item__left {
  width: 90%;
  text-align: left;
}
.menu-left-list .list-item .order-item__left .order__name {
  font-weight: 400;
  font-family: "Economica";
  font-size: 34px;
  color: #ffeeb7;
  margin-bottom: 3px;
}
.menu-left-list .list-item .order-item__left .order__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  font-size: 14px;
  margin-top: 15px;
  letter-spacing: 0.5px;
}
.menu-left-list .list-item .order-item__left .order__desc:hover {
  letter-spacing: 1.2px;
  word-spacing: 1.2px;
  color: #a9a8a6;
}
.menu-left-list .list-item .order-item__right {
  width: 20%;
  text-align: right;
}
.menu-left-list .list-item .order-item__right .order__cost {
  font-weight: 400;
  font-family: "Economica";
  font-size: 34px;
  color: #ffeeb7;
}
.menu-left-list .list-item .order-item__right .order__like {
  margin-top: 25px;
  font-weight: 300;
  font-family: "Quicksand";
  font-size: 14px;
}
.menu-left-list .list-item .order-item__right .order__like i {
  color: red;
  margin-right: 8px;
}
.menu-left .link-view {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  color: #faca36;
  font-size: 18px;
  float: right;
  padding-right: 100px;
  letter-spacing: 1px;
  padding-top: 27px;
  padding-right: 25px;
}
.menu-left .link-view:hover {
  color: #ffffff;
}
.menu-right {
  padding: 0 calc((100% - 1080px) / 2);
  width: 35%;
  background-image: url("/src/img/menu_content.avif");
  background-repeat: no-repeat;
  background-size: cover;
}

.special {
  height: 1647px;
  text-align: center;
  box-sizing: border-box;
  padding: 88px calc((100% - 1080px) / 2);
}
.special__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  letter-spacing: 1.2px;
}
.special__title span {
  color: #101311;
  font-family: "Economica";
}
.special__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  padding: 12px 12.5% 44px;
  line-height: 1.5;
  word-spacing: 0.7px;
  letter-spacing: 0.7px;
}
.special__artical-item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
}
.special__artical-item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.special__artical-item .content {
  padding: 90px;
  text-align: left;
  display: flex;
  flex-direction: column;
  word-spacing: 1px;
  letter-spacing: 0.3px;
  background-color: #fcf4ea;
}
.special__artical-item .content__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  font-weight: 400;
  font-family: "Economica";
  font-size: 36px;
  color: #e3b359;
  margin: 7px 0 16px 0;
}
.special__artical-item .content__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  line-height: 1.5;
  letter-spacing: 0.6px;
  margin-bottom: 5px;
}
.special__artical-item:nth-child(2) img {
  order: 2;
}

.infor {
  height: 447px;
  background-image: linear-gradient(to right, rgb(0, 0, 0), rgba(0, 0, 0, 0.9), rgba(230, 218, 212, 0)), url("/img/coffee-beans.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 98px calc((100% - 1080px) / 2);
}
.infor__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  letter-spacing: 1.2px;
}
.infor__title span {
  color: #ffffff;
  font-family: "Economica";
}
.infor__desc {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  line-height: 1.8;
  word-spacing: 0.5px;
  letter-spacing: 0.7px;
  padding: 12px 0;
  width: 74%;
  color: #ffffff;
}
.infor button {
  background-color: transparent;
  border: 2px solid #faca36;
  border-radius: 4px;
  height: 45px;
  margin-top: 12px;
  width: 18%;
}
.infor button a {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  color: #faca36;
  letter-spacing: 0.8px;
}
.infor button:hover {
  background-color: #faca36;
}
.infor button:hover a {
  color: #101311;
}

.contact {
  height: 624px;
  padding: 91px 90px;
  text-align: center;
  color: #101311;
  padding: 92px calc((100% - 1080px) / 2);
}
.contact__title {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Economica";
  color: #faca36;
  font-size: 50px;
  letter-spacing: 1.2px;
  margin-bottom: 28px;
  color: #101311;
}
.contact-infor {
  display: grid;
  text-align: left;
  grid-template-columns: 1fr 1.04fr;
}
.contact-infor .address ul {
  list-style-type: none;
}
.contact-infor .address ul i {
  font-size: 22px;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  text-align: center;
  line-height: 38px;
  border: 2px solid #faca36;
  margin-right: 10px;
  color: #faca36;
}
.contact-infor .address ul li {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  margin-bottom: 20px;
  word-spacing: 0.5px;
  letter-spacing: 1px;
}
.contact form input,
.contact form button,
.contact form select {
  height: 40px;
  margin: 10px 0;
  width: 100%;
  padding-left: 10px;
  font-weight: 300;
  font-family: "Quicksand";
  font-size: 20px;
  color: #bfbfbf;
  letter-spacing: 1.5px;
}
.contact form select {
  display: block;
  letter-spacing: 0.2px;
}
.contact form .inline {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.contact form #message {
  height: 156px;
  resize: none;
  width: 100%;
  margin: 8.5px 0;
  padding-left: 10px;
  padding-top: 10px;
  font-weight: 300;
  font-family: "Quicksand";
  font-size: 20px;
  color: #bfbfbf;
  letter-spacing: 1.5px;
}
.contact form button {
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  padding-left: 0px;
  background-color: #faca36;
  color: #000000;
  border: none;
  border-radius: 4px;
}
.contact form button:hover {
  background-color: #000000;
  color: #ffffff;
}

.sub {
  height: 464px;
  background-color: #272926;
  color: #ffffff;
  padding: 90px 0;
  padding-bottom: 103px;
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  padding: 90px calc((100% - 1080px) / 2);
}
.sub img {
  margin: 0 auto;
  padding-top: 10px;
}
.sub__desc {
  line-height: 1.5;
  padding: 20px 7.5%;
  word-spacing: 1.3px;
  letter-spacing: 1.1px;
}
.sub .input-mail {
  background-color: #101311;
  border: none;
  width: 570px;
  margin: 0 auto;
  height: 42px;
  display: flex;
  justify-content: center;
  line-height: 42px;
  padding-left: 13px;
}
.sub .input-mail input {
  background-color: #101311;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
  font-family: "Quicksand";
  height: 42px;
  width: 380px;
  border: none;
}
.sub .input-mail span {
  letter-spacing: 1px;
  width: 140px;
}
.sub .input-mail button {
  background-color: #101311;
  width: 50px;
  border: none;
}
.sub .input-mail button i {
  font-size: 20px;
  color: #ffffff;
  font-size: 16px;
  line-height: 48px;
}

.copyright {
  height: 70px;
  background-color: #faca36;
  font-size: 14px;
  letter-spacing: 0.4px;
  color: #ffffff;
  font-weight: 500;
  font-family: "Quicksand";
  text-align: center;
  line-height: 67px;
  word-spacing: -0.6px;
  padding: 0 calc((100% - 1080px) / 2);
}

.up-btn {
  position: fixed;
  width: 55px;
  height: 55px;
  border-radius: 2px;
  background-color: #2c2c2c;
  cursor: pointer;
  right: 7.2%;
  bottom: 22.5%;
  z-index: 2;
  color: #10132c;
  display: flex;
  align-items: center;
  justify-content: center;
}

.up-btn:hover {
  background: #393a3a;
}

.up-btn i {
  font-size: 30px;
  color: #ffffff;
}

@media screen and (max-width: 1199.98px) {
  header,
.slider,
.service,
.menu-left,
.special,
.infor,
.contact {
    padding: 0 10%;
  }
  header {
    height: 90px;
  }
  header img {
    width: 150px;
    height: 150px;
  }
  header .nav-head {
    line-height: 95px;
  }
  .slider {
    gap: 80px;
    height: 500px;
    padding-bottom: 30px;
  }
  .slider .slider-content {
    margin-top: 75px;
  }
  .slider .slider-content__title {
    font-size: 50px;
  }
  .slider .slider-content__desc {
    margin-bottom: 10px;
  }
  .slider .slider-content .btn-order {
    width: fit-content;
    height: fit-content;
    padding: 0 10px;
  }
  .service {
    height: 700px;
    padding-top: 5%;
  }
  .service__desc {
    font-size: 17px;
  }
  .service .service-list .service-item__desc {
    font-size: 17px;
  }
  .menu {
    height: 700px;
  }
  .menu .menu-left {
    padding-top: 5%;
    width: 60%;
    padding-right: 5%;
  }
  .menu .menu-left__title {
    width: 100%;
  }
  .menu .menu-left__service {
    width: 100%;
  }
  .menu .menu-left__service-btn {
    width: 40%;
  }
  .menu .menu-left__service-btn a {
    font-size: 15px;
  }
  .menu .menu-left-list {
    width: 100%;
    gap: 20px;
  }
  .menu .menu-left-list .list-item .order-item .order__name {
    font-size: 30px;
  }
  .menu .menu-left-list .list-item .order-item .order__cost {
    font-size: 30px;
  }
  .menu .menu-left-list .list-item .order-item .order__desc {
    font-size: 12px;
  }
  .menu .menu-left-list .list-item .order-item .order__like i {
    font-size: 10px;
  }
  .menu .menu-right {
    width: 40%;
  }
  .special {
    height: 1250px;
    padding-top: 5%;
  }
  .special__title {
    font-size: 50px;
  }
  .special__desc {
    font-size: 17px;
  }
  .special__artical-item .content {
    padding: 10%;
  }
  .special__artical-item .content__desc {
    font-size: 15px;
  }
  .infor {
    height: 380px;
    padding-top: 5%;
  }
  .infor__title {
    font-size: 50px;
  }
  .infor__desc {
    font-size: 17px;
  }
  .contact {
    padding-top: 5%;
    height: 500px;
  }
  .contact-infor {
    grid-template-columns: 1.2fr 1fr;
    gap: 10px;
  }
  .contact-infor .address ul li {
    font-size: 15px;
  }
  .contact-infor form .inline {
    display: block;
  }
  .contact-infor form input,
.contact-infor form select {
    height: 30px;
    font-size: 17px;
  }
  .contact-infor form #message {
    height: 90px;
    font-size: 17px;
  }
  .sub {
    height: 350px;
    padding-top: 5%;
  }
  .sub__desc {
    font-size: 17px;
  }
}
@media screen and (max-width: 899.98px) {
  .navbar {
    display: block;
    color: #ffffff;
    font-size: 20px;
  }
  header {
    height: 70px;
  }
  header .logo-head {
    padding: 0;
  }
  header img {
    width: 70px;
    height: 70px;
  }
  header .nav-head .nav-item {
    display: none;
  }
  .slider {
    gap: 30px;
    height: 300px;
    padding-bottom: 10px;
    text-align: center;
  }
  .slider .slider-content {
    margin-top: 25px;
    margin: 0;
    width: 100%;
    justify-content: center;
  }
  .slider .slider-content__title {
    font-size: 34px;
  }
  .slider .slider-content__desc {
    margin-bottom: 10px;
    font-size: 15px;
  }
  .slider .slider-content .btn-order {
    margin: 0 auto;
  }
  .slider .slider-content .btn-order a {
    font-size: 15px;
    letter-spacing: 0;
    line-height: 30px;
  }
  .slider .dot button {
    height: 7px;
    width: 7px;
  }
  .service {
    height: 100%;
    padding: 5%;
  }
  .service__title {
    font-size: 34px;
  }
  .service__desc {
    font-size: 14px;
    padding: 5px 0;
  }
  .service .service-list {
    display: grid;
    justify-items: center;
  }
  .service .service-list .service-item {
    width: 100%;
  }
  .service .service-list .service-item__title {
    font-size: 30px;
    padding: 0;
  }
  .service .service-list .service-item__desc {
    font-size: 14px;
    width: 100%;
    padding: 0;
  }
  .service .service-list .service-item img {
    height: 200px;
    width: 200px;
  }
  .menu {
    height: 100%;
  }
  .menu .menu-left {
    padding-top: 5%;
    width: 100%;
    padding-right: 5%;
  }
  .menu .menu-left__title {
    width: 100%;
    font-size: 34px;
  }
  .menu .menu-left__service {
    width: 100%;
  }
  .menu .menu-left__service-btn {
    width: 40%;
  }
  .menu .menu-left__service-btn a {
    font-size: 13px;
  }
  .menu .menu-left-list {
    width: 100%;
  }
  .menu .menu-left-list .list-item .order-item .order__name {
    font-size: 20px;
  }
  .menu .menu-left-list .list-item .order-item .order__cost {
    font-size: 20px;
  }
  .menu .menu-left-list .list-item .order-item .order__desc {
    font-size: 10px;
  }
  .menu .menu-left-list .list-item .order-item .order__like i {
    font-size: 10px;
  }
  .menu .menu-left .link-view {
    font-size: 15px;
    padding-bottom: 5%;
  }
  .menu .menu-right {
    display: none;
  }
  .special {
    height: 100%;
    padding: 5%;
  }
